export default {
	data() {
		return {
			user: '',
			psw: '',
			repsw: ''
		}
	},
	onLoad() {

	},
	methods: {
		login() {
			if (this.user.length <= 0) {
				uni.showToast({
					icon: 'none',
					title: '请输入用户名'
				})
				return
			}
			if (this.psw.length <= 3) {
				uni.showToast({
					icon: 'none',
					title: '密码不能为空:长度4-16位'
				})
				return
			}
			if (this.repsw.length <= 3) {
				uni.showToast({
					icon: 'none',
					title: '密码不能为空:长度4-16位'
				})
				return
			}

			if (this.psw !== this.repsw) {
				uni.showToast({
					icon: 'none',
					title: '两次输入的密码不一样'
				})
				return
			} else {
				uni.request({
					url: 'http://192.168.31.9/api/user/register',
					data: {
						username: this.user,
						pwd: this.psw,
						repwd: this.repsw
					},
					method:'POST',
					success: (res) => {
						if (this.user !== '') {
							var reg = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
							if (!reg.test(this.user)) {
								uni.showToast({
									icon: 'none',
									title: '请输入有效的邮件'
								})
							}
						}
						console.log(res)

						if (res.data.ret == 200) {
							uni.showToast({
								icon: 'none',
								title: '注册成功'
							})
						
						} else {
							uni.showToast({
								icon: 'none',
								title: '邮箱被注册'
							})
						}

					}
				})
			}
		}
	}
}
